*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.weather{
    height: 100vh;
    background-color: #0093E9;
    background-image: linear-gradient(121deg, #0093E9 0%, #80D0C7 100%);
    display: flex;
    justify-content: center;
    align-items: center;
}

.App{
    width: 500px;
    background-color: skyblue;
    border-radius: 25px;
    padding: 30px 25px;
}

.weather_title{
    text-align: center;
    font-style: inherit ;
    color: white;
    text-shadow: 1px 1px 15px black;
    font-size: 45px;
    letter-spacing: 22px;
    margin-bottom: 20px;
}

.weather_form{

}

.weather_input {
    width: 60%;
    height: 40px;
    letter-spacing: 2px;
    font-weight: bold;
    padding-left: 5px;
    outline: none;

}

.weather_button{
    width: 40%;
    height: 40px;
    cursor: pointer;
}

.weather__city{
    margin: 20px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.weather__temperature{
    display: flex;
    font-size: 70px;
    font-weight: bold;
    align-items: center;
}

.weather__switch{
    display: flex;
    font-size: 20px;
    align-self: flex-start;
    align-items: center;
}

span{
    cursor: pointer;
    color: white;
}

span.active{
    color: red;
}